import { randomId } from '../../utils/randomId.js';
export default {
  name: 'NumberInput',
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: Number,
      default: undefined
    },
    allowEmpty: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: randomId()
    };
  },
  computed: {
    realValue() {
      if (this.allowEmpty) {
        return this.value;
      }
      return this.value ?? 0;
    }
  },
  methods: {
    change(e) {
      this.$emit('update:value', parseInt(e.target.value));
    }
  }
};