import { generateUrl } from '@nextcloud/router';
export default {
  name: 'OrganizerNoEmailError',
  computed: {
    /**
     * This returns the caption of the warning message, including a link to the personal settings
     *
     * @return {string}
     */
    htmlCaption() {
      return this.$t('calendar', 'To send out invitations and handle responses, [linkopen]add your email address in personal settings[linkclose].').replace('[linkopen]', `<a target="_blank" href="${generateUrl('settings/user')}">`).replace('[linkclose]', '</a>');
    }
  }
};