import { NcButton } from '@nextcloud/vue';
import CheckIcon from 'vue-material-design-icons/Check.vue';
export default {
  name: 'SaveButtons',
  components: {
    NcButton,
    CheckIcon
  },
  props: {
    canCreateRecurrenceException: {
      type: Boolean,
      required: true
    },
    isNew: {
      type: Boolean,
      required: true
    },
    isReadOnly: {
      type: Boolean,
      required: true
    },
    forceThisAndAllFuture: {
      type: Boolean,
      required: true
    },
    showMoreButton: {
      type: Boolean,
      default: false
    },
    moreButtonType: {
      type: String,
      default: undefined
    },
    growHorizontally: {
      type: Boolean,
      default: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    showSaveButton() {
      return !this.isReadOnly && this.isNew && !this.canCreateRecurrenceException;
    },
    showUpdateButton() {
      return !this.isReadOnly && !this.isNew && !this.canCreateRecurrenceException;
    },
    showUpdateOnlyThisButton() {
      return !this.isReadOnly && this.canCreateRecurrenceException && !this.forceThisAndAllFuture;
    },
    showUpdateThisAndFutureButton() {
      return !this.isReadOnly && this.canCreateRecurrenceException;
    }
  },
  methods: {
    saveThisOnly() {
      this.$emit('save-this-only');
    },
    saveThisAndAllFuture() {
      this.$emit('save-this-and-all-future');
    },
    showMore() {
      this.$emit('show-more');
    }
  }
};