import autosize from '../../../directives/autosize.js';
import PropertyMixin from '../../../mixins/PropertyMixin.js';
import { Linkify } from '@nextcloud/vue';
import InformationVariant from 'vue-material-design-icons/InformationVariant.vue';
import PropertyLinksMixin from '../../../mixins/PropertyLinksMixin.js';
export default {
  name: 'PropertyText',
  directives: {
    autosize,
    Linkify,
    InformationVariant
  },
  mixins: [PropertyMixin, PropertyLinksMixin],
  props: {
    isDescription: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    display() {
      if (this.isReadOnly) {
        if (typeof this.value !== 'string') {
          return false;
        }
        if (this.value.trim() === '') {
          return false;
        }
      }
      return true;
    },
    /**
     * Returns the default number of rows for a textarea.
     * This is used to give the description field an automatic size 2 rows
     *
     * @return {number}
     */
    rows() {
      return this.propModel.defaultNumberOfRows || 1;
    }
  },
  methods: {
    changeValue(event) {
      if (event.target.value.trim() === '') {
        this.$emit('update:value', null);
      } else {
        this.$emit('update:value', event.target.value);
      }
    }
  }
};