import { randomId } from '../../utils/randomId.js';
export default {
  name: 'TextInput',
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      id: randomId()
    };
  },
  methods: {
    change(e) {
      this.$emit('update:value', e.target.value);
    }
  }
};