import DurationSelect from './DurationSelect.vue';
import { randomId } from '../../utils/randomId.js';
export default {
  name: 'CheckedDurationSelect',
  components: {
    DurationSelect
  },
  props: {
    label: {
      type: String,
      required: true
    },
    value: {
      type: Number,
      default: 0
    },
    defaultValue: {
      type: Number,
      default: 0
    },
    enabled: {
      type: Boolean,
      required: true
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: [Number, null, undefined],
      default: 60 * 60
    }
  },
  data() {
    return {
      id: randomId()
    };
  },
  computed: {
