import { isRTL, getLanguage } from '@nextcloud/l10n';
import focus from '../../../directives/focus.js';
export default {
  name: 'PropertyTitle',
  directives: {
    focus
  },
  props: {
    isReadOnly: {
      type: Boolean,
      required: true
    },
    value: {
      type: String,
      default: ''
    }
  },
  computed: {
    isRTL() {
      return isRTL(getLanguage());
    }
  },
  methods: {
    changeValue(event) {
      this.$emit('update:value', event.target.value);
    }
  }
};