import { NcButton, NcModal as Modal } from '@nextcloud/vue';
import ImportScreenRow from './ImportScreenRow.vue';
export default {
  name: 'ImportScreen',
  components: {
    NcButton,
    ImportScreenRow,
    Modal
  },
  props: {
    files: {
      type: Array,
      required: true
    }
  },
  computed: {
    headerRowKey() {
      return this._uid + '-header-row';
    }
  },
  methods: {
    importCalendar() {
      this.$emit('import-calendar');
    },
    cancelImport() {
      this.$emit('cancel-import');
    }
  }
};