import { randomId } from '../../utils/randomId.js';
export default {
  name: 'SelectWrapper',
  props: {
    label: {
      type: String,
      default: ''
    },
    value: {
      type: [String, Number],
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    options: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      id: randomId()
    };
  },
  methods: {
    onSelect(e) {
      this.$emit('update:value', e.target.value);
    }
  }
};